var CryptoJS = require("crypto-js");

class Block {
    constructor(data, previousHash = '') {
        this.data = data;
        this.previous_hash = previousHash;
        this.hash = this.calculateHash();
    }

    calculateHash() {
        return CryptoJS.SHA256(this.previous_hash + this.data).toString();
    }
}

// add genesis block
var genesisBlock = new Block("Genesis Block");
console.log(genesisBlock);

// add new block
function addBlock(data, previousBlock) {
    if (!previousBlock) {
        throw new Error("Previous block is undefined");
    }
    var block = new Block(data, previousBlock.hash);
    return block;
}


module.exports = { Block, addBlock };