import React, { useState } from 'react';
import { Block, addBlock } from './main';
import './App.css';

function App() {
    const [blocks, setBlocks] = useState([new Block("Genesis Block")]);
    const [blockData, setBlockData] = useState('');
    const [error, setError] = useState(null);

    const addNewBlock = () => {
        if (blockData.trim() === '') {
            alert('Block data cannot be empty');
            return;
        }
        try {
            const newBlock = addBlock(blockData, blocks[blocks.length - 1]);
            setBlocks([...blocks, newBlock]);
            setBlockData(''); // Clear the input field
        } catch (err) {
            setError(err.message);
        }
    };

    const deleteLastBlock = () => {
        if (blocks.length === 1) {
            alert('Cannot delete the Genesis Block');
            return;
        }
        setBlocks(blocks.slice(0, -1));
    };

    return (
        <div className="App">
            <h1><i>Demonstration of Blockchain</i></h1>
            {error && (
                <div className="error">
                    <p>Error: {error}</p>
                    <button onClick={() => setError(null)}>Dismiss</button>
                </div>
            )}
            <div>
                <input
                    type="text"
                    value={blockData}
                    onChange={(e) => setBlockData(e.target.value)}
                    placeholder="Enter block data"
                />
                <button onClick={addNewBlock}>Add Block</button>
                <button onClick={deleteLastBlock} style={{ marginLeft: '10px', backgroundColor: 'red' }}>Delete Last Block</button>
            </div>
            <div>
                {blocks.map((block, index) => (
                    <div key={index} className="block">
                        <p><strong>Block {index + 1}</strong></p>
                        <p><strong>Data:</strong> <i>{block.data}</i></p>
                        <p><strong>Hash:</strong> <i>{block.hash}</i></p>
                        <p><strong>Previous Hash:</strong> <i>{block.previous_hash}</i></p>
                    </div>
                ))}
            </div>
        </div>
    );
}

export default App;
